import { Data } from "@/interfaces/course";
import CourseProviderWrapper from "./components/CourseProviderWrapper";
import { getCourseData } from "@/lib/getCourseData";

export async function getStaticProps(){
  const data = await getCourseData('en');

  return {
    props: {
      initialData: data,
    },
    revalidate: 60
  }
}

export default function Home({ initialData }: {initialData: Data}) {
  return <CourseProviderWrapper initialData={initialData} />;
}
